  export const deleteUser = ({adminId,userID}) => dispatch => {
    
    dispatch({
      type:  ADMIN_DELETING_USER
    });
    axios
      .delete(`/api/admin/${adminId}/delete/${userID}`)
      .then(res =>{
        alert("Deleted Successfully")
        dispatch({
          type:   ADMIN_DELETED_USER
        })
        window.location.href= "."
        // window.location.reload()

        // history.push("/organisation/dashboard")
      }
      )
      .catch(err => {
        // dispatch(
        //   returnErrors(err.response.data, err.response.status, "FLAG FAILED")
        // );
        alert("Delete failed")
      });

  };




  
  /** FLAG FUNCTIONALITY */

  export const flagUser = ({adminId,userID}) => dispatch => {
    
    dispatch({
      type:  ADMIN_FLAGING_USER
    });
    axios
      .put(`/api/admin/${adminId}/flag/${userID}`)
      .then(res =>{
        alert("USER FLAGED SUCCESSFULLY")
        dispatch({
          type:   ADMIN_FLAGED_USER
        })
        window.location.href= "."
      }
      )
      .catch(err => {
        alert("Flag Failed")
        console.log(err.response.data)
      });

  };


  
